import React from 'react';
import Image from 'next/image';
import whatsappQR from 'public/static/img/whatsappQR.jpg';

import closeDark from 'public/static/img/closeDark.svg';

import styles from './ChatButton.module.scss';
import { useRouter } from 'next/router';
import { useTranslation } from 'next-i18next';
import { SuisseIntl } from 'helpers/utils/fonts';

type ChatButtonTypes = {
	refImage?: React.RefObject<HTMLDivElement>;
	chatRef?: React.RefObject<HTMLDivElement>;
	mobileImage?: React.RefObject<HTMLDivElement>;
	isNotOpacity?: boolean;
	inCard?: boolean;
};

const ChatButton = ({ refImage, chatRef, mobileImage, isNotOpacity, inCard }: ChatButtonTypes) => {
	const [chatIsOpen, setChatIsOpen] = React.useState<boolean>(false);
	const [footerRight, setFooterRight] = React.useState<number>(0);
	const [windowInnerWidth, setWindowInnerWidth] = React.useState<number>(0);

	const router = useRouter();

	const { t } = useTranslation();

	const chatOpenHandler = () => {
		if (chatIsOpen) {
			if (refImage && refImage.current) refImage.current.style.zIndex = '0';
		} else {
			if (refImage && refImage.current) refImage.current.style.zIndex = '10000';
		}
		setChatIsOpen(!chatIsOpen);
	};

	const hiddenChat = () => {
		const chat = document.querySelector<HTMLDivElement>(`.${styles.chat}`);

		if (chat === null) return;
		if (chat.getBoundingClientRect().top + window.scrollY < 715) {
			chat.style.opacity = '0';
		} else {
			chat.style.opacity = '1';
		}
	};

	React.useEffect(() => {
		const footer = document.querySelector('footer > div:first-child');

		const footerObserver = new ResizeObserver(entries => {
			const offsetRight = entries[0].contentRect.right;
			if (footerRight !== offsetRight) {
				setFooterRight(offsetRight);
			}
		});

		footerObserver.observe(footer);
	}, [footerRight]);

	React.useEffect(() => {
		const updateWindowInnerWidth = () => setWindowInnerWidth(window.innerWidth);
		updateWindowInnerWidth();
		window.addEventListener('resize', updateWindowInnerWidth);
		return () => window.removeEventListener('resize', updateWindowInnerWidth);
	}, []);

	React.useEffect(() => {
		if (router.pathname !== '/') return;
		document.addEventListener('scroll', hiddenChat);
		return () => document.removeEventListener('scroll', hiddenChat);
	}, [router.pathname]);

	React.useEffect(() => {
		if (chatRef && chatRef.current) {
			if (mobileImage.current) {
				if (
					mobileImage.current.nextElementSibling.getBoundingClientRect().top >=
					document.documentElement.clientHeight - 60
				) {
					chatRef.current.style.top = `${
						mobileImage.current.getBoundingClientRect().bottom -
						(mobileImage.current.getBoundingClientRect().bottom - document.documentElement.clientHeight) -
						135
					}px`;
				} else {
					chatRef.current.style.top = `${mobileImage.current.getBoundingClientRect().bottom - 65}px`;
				}
			}
		}
	}, [inCard, chatRef, mobileImage]);

	const emailBlockWidth = 1655;
	const chatIsNotOpenWidth = 70;
	const chatIsOpenWidth = 388;
	const chatIsNotOpenOffset = 24;

	const chatStyle = {
		left:
			windowInnerWidth > emailBlockWidth
				? `${emailBlockWidth - chatIsNotOpenWidth - chatIsNotOpenOffset + (windowInnerWidth - emailBlockWidth) / 2}px`
				: `${footerRight - (chatIsOpen ? chatIsOpenWidth : chatIsNotOpenWidth)}px`,
	};

	return (
		<div style={chatStyle} ref={chatRef} className={`${styles.chat} ${isNotOpacity ? styles.isNotOpacity : ''}`}>
			{!chatIsOpen ? (
				<button onClick={chatOpenHandler} className={`${styles.defaultBtn} ${SuisseIntl.className}`}>
					{t('chatButton.name')}
				</button>
			) : (
				<div className={`${styles.info} ${SuisseIntl.className}`}>
					<button onClick={chatOpenHandler} className={styles.closeBtn}>
						<Image
							alt="X"
							src={closeDark}
							width={24}
							height={24}
							style={{
								maxWidth: '100%',
								height: 'auto',
							}}
						/>
					</button>
					<div className={styles.data}>
						<p>{t('chatButton.question')}</p>
						<div className={styles.contacts}>
							<p>Whatsapp</p>
							<a target="_blank" href="https://api.whatsapp.com/send?phone=79151275549" rel="noreferrer">
								+7 915 127-55-49
							</a>
						</div>
					</div>
					<a
						target="_blank"
						href="https://api.whatsapp.com/send?phone=79151275549"
						className={styles.mobileBtn}
						rel="noreferrer"
					>
						<button className={SuisseIntl.className}>{t('chatButton.button')}</button>
					</a>
					<Image
						alt="Whatsapp"
						src={whatsappQR}
						width={150}
						height={130}
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
				</div>
			)}
		</div>
	);
};

export default React.memo(ChatButton);
